.main_container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.main_header {
  display: flex;
  position: fixed;
  white-space: nowrap;
  flex-shrink: 0;
  width: 100%;
  align-items: center;
  height: 58px;
  background-color: white;
  border-bottom: 2px solid #e8e8e8;
  margin-top: 60px;
  padding: 0px 120px;
}
.main_header > a {
  color: #212529;
}

.main_header .header_menu > a {
  font-size: 16px;
}
.main_header .header_menu > a :hover {
  color: #72efdd;
}
.main_header .header_menu {
  margin-left: 250px;
}

.content_wrapper {
  display: flex;
  flex-direction: column;
  color: black;
  height: 100%;
  overflow: auto;
  margin-top: 120px;
  padding: 50px;
  background-color: #ced4da;
}

.content_wrapper_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 30px;
  background-image: url("https://www.transparenttextures.com/patterns/cubes.png"),
    linear-gradient(
      to right top,
      #cf4af3,
      #e73bd7,
      #f631bc,
      #fd31a2,
      #ff3a8b,
      #ff4b78,
      #ff5e68,
      #ff705c,
      #ff8c51,
      #ffaa49,
      #ffc848,
      #ffe652
    );
  border-radius: 14px;
}

.content_wrapper_context {
  max-width: 400px;
  padding-left: 20px;
}

.content_wrapper_context > h3 {
  color: white;
  font-weight: 600;
  padding-bottom: 10px;
  font-size: 17px;
  display: flex;
  align-items: center;
  margin: 0;
}

.img_content {
  display: flex;
  font-size: 17px;
  align-items: center;
}

.content_text {
  font-size: 15px;
  line-height: 21px;
  margin-bottom: 20px;
  margin-top: 7px;
  color: white;
}

.content_button {
  border: none;
  padding: 10px 20px;
  background-color: #3af1dd;
  color: white;
  font-size: 15px;
  border-radius: 38px;
  white-space: nowrap;
}

.content-wrapper-img {
  width: 186px;
  object-fit: cover;
  object-position: center;
  margin-top: -30px;
}
