.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 58px;
  position: fixed;
  white-space: nowrap;
  flex-shrink: 0;
  background-color: white;
  border-bottom: 2px solid #e8e8e8;
}

.header_menu {
  display: flex;
  align-items: center;
}

.header_logo {
  margin-right: 100px;
  margin-left: 76px;
}

.header_logo > a {
  color: #eb292e;
  text-decoration: none;
  font-weight: 800;
}
.header_menu > a {
  padding: 18px 30px;
  text-decoration: none;
  color: #212529;
  font-weight: 500;
  border-bottom: 2px solid transparent;
}

.header .header_menu > a {
  padding: 18px 30px;
  text-decoration: none;
  color: #212529;
  border-bottom: 2px solid transparent;
}

.header_menu > a:hover {
  color: #72efdd;
  border-bottom: 2px solid #72efdd;
}

.header_search {
  width: 100%;
  padding-left: 16px;
  height: 40px;
  display: flex;
  max-width: 450px;
  border-radius: 4px;
}

.header_search > input {
  height: 100%;
  padding: 0 20px 0 40px;
  width: 100%;
  color: #e8e8e8;
  border: none;
  font-size: 16px;
  background-size: 14px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 56.966 56.966' fill='%23717790c7'%3e%3cpath d='M55.146 51.887L41.588 37.786A22.926 22.926 0 0046.984 23c0-12.682-10.318-23-23-23s-23 10.318-23 23 10.318 23 23 23c4.761 0 9.298-1.436 13.177-4.162l13.661 14.208c.571.593 1.339.92 2.162.92.779 0 1.518-.297 2.079-.837a3.004 3.004 0 00.083-4.242zM23.984 6c9.374 0 17 7.626 17 17s-7.626 17-17 17-17-7.626-17-17 7.626-17 17-17z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: 16px 48%;
  background-color: #212529;
  border-radius: 4px;
}

.header_profile {
  display: flex;
  align-items: center;
  padding: 0 53px 0 60px;
}

.header_profile svg {
  width: 22px;
  color: white;
}

.notification {
  position: relative;
}

.notification_number {
  position: absolute;
  background-color: #72efdd;
  width: 16px;
  height: 16px;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 10px;
  border-radius: 50%;
  color: white;
  right: -6px;
  top: -6px;
}

.notification + svg {
  margin-left: 22px;
}

.image_profile {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-left: 22px;
  border: 2px solid #72efdd;
}
