.side_wrapper {
  margin-top: 25px;
}

.side_title {
  color: rgb(33 37 41 / 42%);
  margin-bottom: 14px;
  font-weight: 500;
}

.side_menu {
  display: flex;
  flex-direction: column;
  white-space: nowrap;
}

.side_menu > a {
  text-decoration: none;
  display: flex;
  padding: 10px;
  font-weight: 400;
  border-radius: 6px;
  align-items: center;
  color: #212529;
}

.side_menu > a:hover {
  background-color: rgba(114, 239, 221, 0.36);
  color: white;
}

.side_menu svg {
  width: 20px;
  margin-right: 15px;
}
